    getQueryString(params: any): string {
      return Object.entries(params)
      .map(
        ([key, value]) =>
          `${encodeURIComponent(key)}=${encodeURIComponent(String(value))}`
      )
      .join("&");
    }

    getProjects(clientID: string): Observable<any> {
      let url = `${this.gatewayApp}/get_onboarding`;
      const params = {
        'client_id': clientID,
        'user_id': sessionStorage.getItem('user_id')
      }
      return this.http.post(url, params).pipe(map(data=>{
        return JSON.parse(data["body"]);
      }))
    }


    postData(params: any,body: any): Observable<any> {
      const queryString = this.getQueryString(params);
      const fullUrl = `${this.gatewayApp}/create_project?${queryString}`;
      return this.http.post(fullUrl, body).pipe(map(data=>{
        return data;
      }));
    }

