import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { InConfiguration } from '../core/models/config.interface';

@Injectable({
  providedIn: 'root'
})
export class ConfigService {
  public configData: InConfiguration;
  private configSubject = new BehaviorSubject<InConfiguration>(this.loadInitialConfig());
  public config$ = this.configSubject.asObservable();

  constructor() {
  }
  private loadInitialConfig(): InConfiguration {
    const storedConfig = localStorage.getItem('appConfig');
    return storedConfig ? JSON.parse(storedConfig) : this.getDefaultConfig();
  }
  private getDefaultConfig(): InConfiguration {
    return {
      layout: {
        rtl: false,
        variant: 'light',
        theme_color: 'white',
        logo_bg_color: 'white',
        sidebar: {
          collapsed: false,
          backgroundColor: 'light'
        }
      }
    };
  }

  setConfigData(newConfig: InConfiguration): void {
    this.configSubject.next(newConfig);
    localStorage.setItem('appConfig', JSON.stringify(newConfig));
  }

  getCurrentConfig(): InConfiguration {
    return this.configSubject.getValue();
  }
}
